import React, { Component } from 'react';

// PHN 1: IMPORT


import MenuDieuKhien from './MenuDieuKhien'

//Danh ba tat ca Phim



// PHN 2: State
class Home extends Component {
  state = {
    
  }

// PHN 3: Function





// PHN 4: Trnh by trang Web, ging HTML
  render() {
    var {} = this.state;
    var { chieuRongManHinh, dangXemGi, white_or_black, hienVaGiauPhoneMenu } = this.props;
    return (
      <div className="Home" style={{height:'100vh', color:  white_or_black ? 'red' : 'blue'}}>

        <h1>y l trang web ca DIMA</h1>
        <h2>Truyn, Phim v nh</h2>

        {(chieuRongManHinh > 900) /*Kim tra l My Tnh hay l in Thoi? */ 
          ? null
          : <MenuDieuKhien dangXemGi={dangXemGi} white_or_black={white_or_black} chieuRongManHinh={chieuRongManHinh} 
                            hienVaGiauPhoneMenu={hienVaGiauPhoneMenu}/>
        }
        
        

      </div>
    )
  }
}
export default Home;